body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section#input-mood {
  height: 40vh;
  width: 40vw;
  margin: 2vh;
  font-size: 16px;
  display: flex;
  flex-flow: column nowrap;
  -moz-justify-content: space-around;
  -ms-justify-content: space-around;
  justify-content: space-around;
  -ms-flex-pack: space-around;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  border-radius: 10px;
  border: none;
  background-color: #fff;
}
section#input-mood::after {
  content: " ";
  position: relative;
  border-bottom: 3px solid #feefef;
}
section#input-mood input {
  width: 200px;
  padding: 5px;
  text-align: center;
  font-size: 1.4em;
  border: none;
  border-bottom: 2px solid #68466f;
  outline: none;
}
section#input-mood input::-webkit-inner-spin-button, section#input-mood input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
section#input-mood select {
  height: 50px;
  font-size: 1.4em;
}
section#input-mood button {
  height: 50px;
  width: 150px;
  color: #68466f;
  font-size: 1.25em;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

